
import Vue from 'vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import Group from 'vux/src/components/group/index.vue'
import XInput from 'vux/src/components/x-input/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'
import AjaxPlugin from 'vux/src/plugins/ajax/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'

import * as base from '@/api/base'

Vue.use(ToastPlugin)
Vue.use(AjaxPlugin)

export default {
components: {
XHeader,
Group,
XInput,
XButton,
Box
},
data () {
return {
bankCode: base.getSingleUserInfo('user_bankCode') || '',
bankCardholder: base.getSingleUserInfo('user_bankCardholder'),
bankOpening: base.getSingleUserInfo('user_bankOpening'),
bankName: base.getSingleUserInfo('user_bankName'),
phoneNumber: base.getSingleUserInfo('user_phone'),
sendCodeText: '验证码',
authCode: ''
}
},
methods: {
submitA: function () {
if (this.bankOpening === '') {
this.$vux.toast.text('请输入开户行', 'middle')
return false
}
if (this.bankName === '') {
this.$vux.toast.text('请输入银行名名称', 'middle')
return false
}
if (this.bankCode === '') {
this.$vux.toast.text('请输入银行卡号', 'middle')
return false
}
if (this.bankCardholder === '') {
this.$vux.toast.text('请输入持卡人姓名', 'middle')
return false
}
if (this.authCode === '') {
this.$vux.toast.text('请输入4位验证码', 'middle')
return false
}
let params = {
'bankopening': this.bankOpening.toString(),
'bankname': this.bankName.toString(),
'bankcode': this.bankCode.toString(),
'bankcardholder': this.bankCardholder.toString(),
'bankphone': this.phoneNumber.toString(),
'authcode': this.authCode.toString()
}
base.fetch('/api/v1/agentbanks', params, response => {
console.log(params)
let data = response.data
let msg = data.msg || data.error || '请求失败'
this.$vux.toast.text(msg, 'middle')
if (data.code !== 0) return false
base.setUserInfo('user_bankName', this.bankName)
base.setUserInfo('user_bankOpening', this.bankOpening)
base.setUserInfo('user_bankCode', this.bankCode)
base.setUserInfo('user_bankCardholder', this.bankCardholder)
base.setUserInfo('user_phone', this.phoneNumber)
base.setUserInfo('user_auth', 3)
this.$router.push('/index/account')
}, error => {
console.log(error)
}, 'POST')
},
sendCode (e) {
if (this.phoneNumber === '') {
this.$vux.toast.text('请填写手机号', 'middle')
return
}
base.fetch('/api/v1/agentbanks/verificationCode', {phone: this.phoneNumber}, response => {
let data = response.data
let msg = data.msg || data.error || '请求失败'
this.$vux.toast.text(msg, 'middle')
if (data.code !== 0) return false
e.target.disabled = 'disabled'
let count = 60
this.sendCodeText = count + '秒'
let interval = setInterval(() => {
count--
this.sendCodeText = count + '秒'
if (count === 0) {
clearInterval(interval)
this.sendCodeText = '验证码'
e.target.removeAttribute('disabled')
}
}, 1000)
}, error => {
console.log(error)
})
}
}
}



